import logo from './logo.svg';
import './App.css';
import { render } from 'react-dom';
import React, {Component} from 'react';
import Acks from './components/acks'

class App extends Component{
  state = {
    acks: []
  }

  componentDidMount() {
    fetch('/ack')
    .then(res => res.json())
    .then((data) => {
      this.setState({acks: data})      
    })
    .catch(console.log)
  }
  
  render() {
    return (
      <div>
        <div>Number of objects : {this.state.acks.length}</div>
        <Acks acks={this.state.acks} />
      </div>
    );
  }
}

export default App;
